<template>
  <div>
    <div class="text-start">
      
      <h3 class="question-number">문제 {{ index + 1 }}</h3>
      <p class="question-text">{{ question }}</p>
      <div class="text-center">

      <img :src="image" alt="" class="img-fluid" />
      </div>
    </div>
    <div class="mt-3 container d-flex justify-content-center">
      <div class="options">
        <div
          class="mt-3 answerBox d-flex flex-column justify-content-between align-items-start"
        >
          <div class="d-flex">
            <p class="ms-2 mb-1">
              <input
                type="radio"
                v-model="selectedAnswer"
                :name="'answer' + index"
                value="a"
                :id="'a' + index"
              />
            </p>
            <div class="text-start">
              <label class="ps-4" :for="'a' + index">{{ answer.a }}</label>
            </div>
          </div>

          <div class="d-flex">
            <p class="ms-2 mb-1">
              <input
                type="radio"
                v-model="selectedAnswer"
                :name="'answer' + index"
                value="b"
                :id="'b' + index"
              />
            </p>

            <div class="text-start">
              <label class="ps-4" :for="'b' + index">{{ answer.b }}</label>
            </div>
          </div>
          <div class="d-flex">

          <p class="ms-2 mb-1">
            <input
              type="radio"
              v-model="selectedAnswer"
              :name="'answer' + index"
              value="c"
              :id="'c' + index"
            />
            <div class="text-start">

            <label class="ps-4" :for="'c' + index">{{ answer.c }}</label>
            </div>
          </div>
          <div class="d-flex">

          <p class="ms-2 mb-1">
            <input
              type="radio"
              v-model="selectedAnswer"
              :name="'answer' + index"
              value="d"
              :id="'d' + index"
            />
            <div class="text-start">

            <label class="ps-4" :for="'d' + index">{{ answer.d }}</label>
            </div>
          </div>
        </div>
        <div class="d-flex justify-content-between mt-3">
          <button
            @click="prevMove"
            class="btn next-btn"
            :class="{ invisible: isFirst }"
          >
            이전 문제
          </button>
          <button
            @click="nextMove"
            class="btn prev-btn"
            :class="{ invisible: isLast }"
          >
            다음 문제
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedAnswer: null,
      image: require(`@/assets/problem${this.index + 1}.png`),
    };
  },
  computed: {
    isFirst() {
      return this.index === 0;
    },
    isLast() {
      return this.index === this.totalCount - 1;
    },
  },
  props: {
    question: String,
    answer: Object,
    index: Number,
    totalCount: Number,

  },
  methods: {
    nextMove() {
      this.$emit("next-move");
    },
    prevMove() {
      this.$emit("prev-move");
    },
    saveAnswer() {
      this.$emit("save-answer", {
        index: this.index,
        selectedAnswer: this.selectedAnswer,
      });
    },
  },


  watch: {
    selectedAnswer() {
      this.saveAnswer();
    },
  },
};
</script>
<style>
.question-number {
  color: #ff69b4;
  font-size: 24px;
  margin-bottom: 10px;
}

.question-text {
  color: #4b0082;
  font-size: 20px;
  margin-bottom: 20px;
  text-shadow: 2px 2px 3px #ffb6c1;
}

.options {
  background-color: #fffacd;
  border-radius: 10px;
  box-shadow: 2px 2px 10px #d2691e;
  padding: 20px;
}

.answerBox {
  border: 1px solid black;
  width: 100%;
  height: 300px;
  text-align: center;
  font-size: 1rem;
}

@media screen and (min-width: 768px) {
  .answerBox {
    width: 800px;
    height: 200px;
  }
  .btn {
    font-size : 16px !important;
  }
}
@media screen and (max-width: 768px) {

  .btn {
    font-size : 5px !important;
  }
}
.btn {
  background-color: #ff69b4;
  border: none;
  border-radius: 10px;
  color: #fff;
 
  font-weight: bold;
  margin-top: 20px;
  padding: 10px 20px;
  text-shadow: 2px 2px 3px #ffb6c1;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  box-shadow: 2px 2px 5px #d2691e;
  cursor: pointer;
  transform: scale(1.1);
}

.invisible {
  display: hidden;
}
</style>
